import React, { useState } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { Plus, Trash2, Type, Mail, Phone, Calendar, Image, FileText, Star, BarChart, CheckSquare, List, Settings, Eye } from 'lucide-react';
import FormPreview from './FormPreview/FormPreview.tsx';

const FormBuilder = ({ onUpdate }) => {
  const [elements, setElements] = useState([]);
  const [theme, setTheme] = useState('Custom Theme');
  const [primaryColor, setPrimaryColor] = useState('#FF00BF');
  const [backgroundColor, setBackgroundColor] = useState('#FFFFFF');
  const [font, setFont] = useState('Inter');
  const [activeTab, setActiveTab] = useState('DESIGN');
  const [showPreview, setShowPreview] = useState(false);
  const [formSettings, setFormSettings] = useState({
    enableRecaptcha: false,
    allowOffline: false,
    showRequired: true,
    enableMultilingual: false,
    autoSave: true,
    redirectUrl: '',
    successMessage: 'Thank you for your submission!',
    validation: {
      emailConfirmation: false,
      preventDuplicates: false,
      fileTypes: ['image/*', 'application/pdf'],
      maxFileSize: 5, // MB
    }
  });

  const elementTypes = [
    { id: 'heading', icon: Type, label: 'Heading' },
    { id: 'text', icon: FileText, label: 'Text Input' },
    { id: 'email', icon: Mail, label: 'Email' },
    { id: 'phone', icon: Phone, label: 'Phone' },
    { id: 'date', icon: Calendar, label: 'Date' },
    { id: 'image', icon: Image, label: 'Image Upload' },
    { id: 'rating', icon: Star, label: 'Rating' },
    { id: 'scale', icon: BarChart, label: 'Scale' },
    { id: 'checkbox', icon: CheckSquare, label: 'Checkbox' },
    { id: 'dropdown', icon: List, label: 'Dropdown' }
  ];

  const handleAddElement = (type) => {
    const newElement = {
      id: Date.now(),
      type,
      label: `New ${type}`,
      required: false,
      options: type === 'dropdown' ? [{ label: '', price: '', id: Date.now() }] : undefined
    };
    setElements([...elements, newElement]);
  };

  const handleElementLabelChange = (elementId, label) => {
    setElements(elements.map(element => {
      if (element.id === elementId) {
        return { ...element, label };
      }
      return element;
    }));
  };

  const handleAddPlan = (elementId) => {
    setElements(elements.map(element => {
      if (element.id === elementId) {
        return {
          ...element,
          options: [...element.options, { label: '', price: '', id: Date.now() }]
        };
      }
      return element;
    }));
  };

  const handleOptionChange = (elementId, optionId, field, value) => {
    setElements(elements.map(element => {
      if (element.id === elementId) {
        return {
          ...element,
          options: element.options.map(option => {
            if (option.id === optionId) {
              return { ...option, [field]: value };
            }
            return option;
          })
        };
      }
      return element;
    }));
  };

  const handleDeleteOption = (elementId, optionId) => {
    setElements(elements.map(element => {
      if (element.id === elementId) {
        const newOptions = element.options.filter(option => option.id !== optionId);
        return {
          ...element,
          options: newOptions.length > 0 ? newOptions : [{ label: '', price: '', id: Date.now() }]
        };
      }
      return element;
    }));
  };

  const handleDeleteElement = (elementId) => {
    setElements(elements.filter(element => element.id !== elementId));
  };

  const onDragEnd = (result) => {
    if (!result.destination) return;

    const items = Array.from(elements);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    setElements(items);
  };

  const handleFormSettingChange = (key, value) => {
    setFormSettings(prev => ({
      ...prev,
      [key]: value
    }));
  };

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Left Panel - Elements */}
      <div className="w-64 bg-white border-r border-gray-200 p-4">
        <h3 className="text-lg font-semibold mb-4">Elements</h3>
        <div className="grid grid-cols-2 gap-2">
          {elementTypes.map((type) => (
            <button
              key={type.id}
              onClick={() => handleAddElement(type.id)}
              className="flex flex-col items-center justify-center p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
            >
              <type.icon className="w-5 h-5 mb-2" />
              <span className="text-xs">{type.label}</span>
            </button>
          ))}
        </div>
      </div>

      {/* Center Panel - Form Builder */}
      <div className="flex-1 p-6 overflow-auto">
        <div className="flex justify-between items-center mb-8">
          <div className="flex items-center space-x-8">
            <div className="flex items-center">
              <div className={`${activeTab === 'DESIGN' ? 'bg-[#FF00BF]' : 'bg-gray-200'} text-white rounded-full h-6 w-6 flex items-center justify-center text-sm`}>
                1
              </div>
              <span className="ml-2 text-sm">Build Form</span>
            </div>
            <div className="flex items-center">
              <div className={`${activeTab === 'PAYMENT' ? 'bg-[#FF00BF]' : 'bg-gray-200'} text-white rounded-full h-6 w-6 flex items-center justify-center text-sm`}>
                2
              </div>
              <span className="ml-2 text-sm">Configure Payment</span>
            </div>
            <div className="flex-1 mx-4 border-t border-gray-200"></div>
            <div className="flex items-center">
              <div className={`${activeTab === 'PREVIEW' ? 'bg-[#FF00BF]' : 'bg-gray-200'} text-white rounded-full h-6 w-6 flex items-center justify-center text-sm`}>
                3
              </div>
              <span className="ml-2 text-sm">Preview</span>
            </div>
          </div>
          <div className="flex items-center space-x-4">
            <button
              onClick={() => setShowPreview(!showPreview)}
              className="flex items-center gap-2 px-4 py-2 text-sm text-gray-600 hover:text-gray-900"
            >
              <Eye className="w-4 h-4" />
              {showPreview ? 'Hide Preview' : 'Show Preview'}
            </button>
            <button
              className="flex items-center gap-2 px-4 py-2 bg-[#FF00BF] text-white rounded-md text-sm"
              onClick={() => setActiveTab('SETTINGS')}
            >
              <Settings className="w-4 h-4" />
              Settings
            </button>
          </div>
        </div>

        {showPreview ? (
          <FormPreview
            elements={elements}
            theme={{ primaryColor, backgroundColor, font }}
          />
        ) : (
          <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId="form-elements">
              {(provided) => (
                <div
                  {...provided.droppableProps}
                  ref={provided.innerRef}
                  className="space-y-4"
                >
                  {elements.map((element, index) => (
                    <Draggable
                      key={element.id}
                      draggableId={element.id.toString()}
                      index={index}
                    >
                      {(provided) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          className="bg-white p-4 rounded-lg shadow-sm border border-gray-200"
                        >
                          <div className="flex justify-between items-center mb-2">
                            <input
                              type="text"
                              value={element.label}
                              onChange={(e) => handleElementLabelChange(element.id, e.target.value)}
                              className="text-lg font-medium bg-transparent border-none focus:outline-none"
                            />
                            <button
                              onClick={() => handleDeleteElement(element.id)}
                              className="text-gray-400 hover:text-red-500"
                            >
                              <Trash2 className="w-4 h-4" />
                            </button>
                          </div>
                          
                          {element.type === 'dropdown' && (
                            <div className="space-y-2">
                              {element.options.map((option) => (
                                <div key={option.id} className="flex gap-2">
                                  <input
                                    type="text"
                                    value={option.label}
                                    onChange={(e) => handleOptionChange(element.id, option.id, 'label', e.target.value)}
                                    placeholder="Option label"
                                    className="flex-1 p-2 border rounded"
                                  />
                                  <input
                                    type="number"
                                    value={option.price}
                                    onChange={(e) => handleOptionChange(element.id, option.id, 'price', e.target.value)}
                                    placeholder="Price"
                                    className="w-24 p-2 border rounded"
                                  />
                                  <button
                                    onClick={() => handleDeleteOption(element.id, option.id)}
                                    className="p-2 text-gray-400 hover:text-red-500"
                                  >
                                    <Trash2 className="w-4 h-4" />
                                  </button>
                                </div>
                              ))}
                              <button
                                onClick={() => handleAddPlan(element.id)}
                                className="flex items-center gap-1 text-sm text-gray-500 hover:text-gray-700"
                              >
                                <Plus className="w-4 h-4" />
                                Add Option
                              </button>
                            </div>
                          )}
                        </div>
                      )}
                    </Draggable>
                  ))}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
        )}
      </div>

      {/* Right Panel - Settings */}
      <div className="w-80 bg-white border-l border-gray-200 p-4 overflow-y-auto">
        <div className="flex space-x-4 mb-6">
          <button
            className={`px-4 py-2 text-sm ${activeTab === 'DESIGN' ? 'text-[#FF00BF] border-b-2 border-[#FF00BF]' : 'text-gray-500'}`}
            onClick={() => setActiveTab('DESIGN')}
          >
            Design
          </button>
          <button
            className={`px-4 py-2 text-sm ${activeTab === 'SETTINGS' ? 'text-[#FF00BF] border-b-2 border-[#FF00BF]' : 'text-gray-500'}`}
            onClick={() => setActiveTab('SETTINGS')}
          >
            Settings
          </button>
        </div>

        {activeTab === 'DESIGN' ? (
          <div className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-gray-700">Theme</label>
              <select
                value={theme}
                onChange={(e) => setTheme(e.target.value)}
                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-[#FF00BF] focus:ring-[#FF00BF]"
              >
                <option>Custom Theme</option>
                <option>Light Theme</option>
                <option>Dark Theme</option>
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700">Primary Color</label>
              <input
                type="color"
                value={primaryColor}
                onChange={(e) => setPrimaryColor(e.target.value)}
                className="mt-1 block w-full"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700">Background Color</label>
              <input
                type="color"
                value={backgroundColor}
                onChange={(e) => setBackgroundColor(e.target.value)}
                className="mt-1 block w-full"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700">Font</label>
              <select
                value={font}
                onChange={(e) => setFont(e.target.value)}
                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-[#FF00BF] focus:ring-[#FF00BF]"
              >
                <option>Inter</option>
                <option>Roboto</option>
                <option>Open Sans</option>
              </select>
            </div>
          </div>
        ) : (
          <div className="space-y-6">
            <div>
              <h3 className="text-sm font-medium text-gray-900 mb-4">Form Settings</h3>
              <div className="space-y-4">
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={formSettings.enableRecaptcha}
                    onChange={(e) => handleFormSettingChange('enableRecaptcha', e.target.checked)}
                    className="rounded border-gray-300 text-[#FF00BF] focus:ring-[#FF00BF]"
                  />
                  <span className="ml-2 text-sm text-gray-600">Enable ReCAPTCHA</span>
                </label>

                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={formSettings.allowOffline}
                    onChange={(e) => handleFormSettingChange('allowOffline', e.target.checked)}
                    className="rounded border-gray-300 text-[#FF00BF] focus:ring-[#FF00BF]"
                  />
                  <span className="ml-2 text-sm text-gray-600">Allow offline submissions</span>
                </label>

                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={formSettings.showRequired}
                    onChange={(e) => handleFormSettingChange('showRequired', e.target.checked)}
                    className="rounded border-gray-300 text-[#FF00BF] focus:ring-[#FF00BF]"
                  />
                  <span className="ml-2 text-sm text-gray-600">Show required markers</span>
                </label>

                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={formSettings.enableMultilingual}
                    onChange={(e) => handleFormSettingChange('enableMultilingual', e.target.checked)}
                    className="rounded border-gray-300 text-[#FF00BF] focus:ring-[#FF00BF]"
                  />
                  <span className="ml-2 text-sm text-gray-600">Enable multilingual support</span>
                </label>
              </div>
            </div>

            <div>
              <h3 className="text-sm font-medium text-gray-900 mb-4">Success Settings</h3>
              <div className="space-y-4">
                <div>
                  <label className="block text-sm text-gray-600 mb-1">Success Message</label>
                  <textarea
                    value={formSettings.successMessage}
                    onChange={(e) => handleFormSettingChange('successMessage', e.target.value)}
                    className="w-full px-3 py-2 border rounded-md"
                    rows={3}
                  />
                </div>

                <div>
                  <label className="block text-sm text-gray-600 mb-1">Redirect URL (optional)</label>
                  <input
                    type="url"
                    value={formSettings.redirectUrl}
                    onChange={(e) => handleFormSettingChange('redirectUrl', e.target.value)}
                    className="w-full px-3 py-2 border rounded-md"
                    placeholder="https://..."
                  />
                </div>
              </div>
            </div>

            <div>
              <h3 className="text-sm font-medium text-gray-900 mb-4">Validation Settings</h3>
              <div className="space-y-4">
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={formSettings.validation.emailConfirmation}
                    onChange={(e) => handleFormSettingChange('validation', {
                      ...formSettings.validation,
                      emailConfirmation: e.target.checked
                    })}
                    className="rounded border-gray-300 text-[#FF00BF] focus:ring-[#FF00BF]"
                  />
                  <span className="ml-2 text-sm text-gray-600">Require email confirmation</span>
                </label>

                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={formSettings.validation.preventDuplicates}
                    onChange={(e) => handleFormSettingChange('validation', {
                      ...formSettings.validation,
                      preventDuplicates: e.target.checked
                    })}
                    className="rounded border-gray-300 text-[#FF00BF] focus:ring-[#FF00BF]"
                  />
                  <span className="ml-2 text-sm text-gray-600">Prevent duplicate submissions</span>
                </label>

                <div>
                  <label className="block text-sm text-gray-600 mb-1">Max File Size (MB)</label>
                  <input
                    type="number"
                    value={formSettings.validation.maxFileSize}
                    onChange={(e) => handleFormSettingChange('validation', {
                      ...formSettings.validation,
                      maxFileSize: parseInt(e.target.value)
                    })}
                    className="w-full px-3 py-2 border rounded-md"
                    min="1"
                    max="50"
                  />
                </div>
              </div>
            </div>

            <div>
              <h3 className="text-sm font-medium text-gray-900 mb-4">Embed Code</h3>
              <div className="bg-gray-50 p-4 rounded-md">
                <pre className="text-sm overflow-x-auto">
                  {`<iframe
  src="your-form-url"
  style="width: 100%; height: 500px; border: none;"
  allow="payment"
></iframe>`}
                </pre>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default FormBuilder;
